import React from 'react';
import { Link } from 'react-router-dom';
import s from './Nav.module.css';

export function Nav() {
  return (
    <nav className={s.nav}>
      <Link to='/' className={s.links}>Reciply</Link>
      <div className={s.buscador}>
        <input type="text" placeholder="Receta..." className={s.input}/>
        <button className={s.buscar}>Buscar</button>
      </div>
      <img src='lupa.png' alt='img'></img>
      <Link to='/add' className={s.links}>Recetas</Link>
    </nav>
  )
};

export default Nav;