import React from 'react';
import Receta from '../Receta/Receta';
import s from './Recetas.module.css'

export default function Recetas(props) {
    console.log('as',props.recetas)
  return (
    <div className={s.recetas}>
      { 
      props.recetas.map(function (r) {
        return(
          <Receta 
            titulo= {r.titulo}
            img={r.img}
            dieta={r.dieta}
            key = {r.id}
          />
      )  
      })
      }
    </div>
  )
};