import React from 'react';
import s from './Receta.module.css';



export default function Receta(props) {
  return (
    <div className={s.receta}>
        <h1>{props.titulo}</h1>
        <img className={s.img} src={props.img} alt='img'></img>
        <h2>{props.dieta}</h2>
    </div>
  )
};